.page-header {
	background-color: @violet;
	padding: 25px 0;
	font-family: @raleway;

	&__logo{
		text-transform: uppercase;
		font-size: @font-p-title;
		font-weight: @font-weight-regular;
		color: @white;
		letter-spacing: 8px;
		svg{
			fill: @pink;
		}
	}

	&__menu{

		a{
			text-transform: uppercase;
			color: @violet-lighter;
			border: 1px solid transparent;					
			&:hover{
				background-color: transparent !important;				
				color:@red;	
				border: 1px solid @red;	
				border-radius: 0;						
			}		
		}
	}

	&__nav{

		.btn{
			background-color: transparent;
			color: transparent;
			display: inline-block;	
			margin-top: 5px;	
			height: 25px;
			width: 25px;		
			background-position: 50% 50%;
			background-repeat: no-repeat;
			margin-left: 10px;													
		}

		.btn--close{				
			background-image: url(data:image/svg+xml;base64,);
		}

		.btn--hamburger{
			background-image: url(data:image/svg+xml;base64,);
		}	

		.nav-visible{
			visibility: visible;
		}
		.nav-invisible{
			visibility: hidden;
		}	
	}

	&--narrow{
		padding: 10px 0;
		transition: all 1s;
	}
}


